@charset "UTF-8";
/*---------------------------------------------------20180822------------------------------------------------------------**/
body,
html {
  width: 100%;
  height: 100%;
  position: relative;
  font-family: '微软雅黑'; }

.login-wrap {
  width: 100%;
  height: 100%;
  position: relative;
  background: url(../images/loginBg.jpg);
  background-size: cover;
  background-position: center; }

.signIn-wrap {
  position: absolute;
  top: 50%;
  left: 50%;
  padding: 30px;
  width: 400px;
  transform: translate(-50%, -50%);
  background: #fff;
  border-radius: 5px;
  box-sizing: border-box; }

.m-title {
  text-align: center;
  font-size: 26px;
  line-height: 52px; }

.login-row {
  position: relative;
  margin: 10px 0; }
  .login-row i {
    position: absolute; }
  .login-row input {
    width: 100%;
    box-sizing: border-box;
    padding-left: 30px;
    height: 40px;
    font-size: 14px;
    border: 1px solid #ddd;
    border-radius: 4px; }
